
.Navigation-left{width:220px;background:#1d2939;height:100%;overflow-y:auto;color:#8f939e
}
.Navigation-left .box{padding:0 10px;overflow:hidden
}
.Navigation-left .box .h3{font-size:16px;font-weight:initial;line-height:36px;height:36px;padding:0 10px;margin-top:10px;cursor:pointer
}
.Navigation-left .box .h3 .icon{margin-top:18px;font-size:20px
}
.Navigation-left .box .h3 .font_{font-size:14px;float:right
}
.Navigation-left .box .h3:hover{background:#fff;color:#1d2939;border-radius:4px
}
.Navigation-left .box .h3hover{background:#fff;color:#1d2939;border-radius:4px
}
.Navigation-left .box ul{padding:0
}
.Navigation-left .box li{list-style:none;padding:5px 15px
}
.Navigation-left .box li .a{width:100%;height:20px;line-height:20px;font-size:12px;color:#fff;text-decoration:none;display:inline-block;cursor:pointer
}
.Navigation-left .box li .a:hover{background:#fff;color:#333333;border-radius:3px
}
.Navigation-left .box li .router-link-active{background:#fff;color:#333333;border-radius:3px
}
.Navigation-left .box li .icon{color:#8f939e;font-size:12px;margin:10px 5px
}
.Navigation-left .box li .icon:hover{color:#fff
}



